@mixin base {
}

.videos-1 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    &__heading {
        text-transform: none !important;
        padding-bottom: 5rem;
    }

    &__para {
        margin: 6rem 0;
        padding: 0 8rem;
    }

    &__video {
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
        height: 176px;
        overflow: hidden;
        width: 321px;
    }

    &__video-container {
        flex-basis: 100%;
        flex-grow: 0;
        flex-shrink: 1;
        margin-bottom: 5rem;
        padding-right: 1rem;
        width: 32.1rem;

        @media screen and (min-width: 1024px) {
            flex-basis: 50%;
            flex-grow: 0;
            flex-shrink: 1;
        }

        @media screen and (min-width: 1200px) {
            flex-basis: auto;
            flex-grow: 0;
            flex-shrink: 1;
            margin: 2rem;
            justify-content: space-between;
            
        }

        h1 {
            display: block;
            margin: 0 auto;
            padding-top: 1rem;
            text-align: left;
        }

        p {
            text-align: left;
        }
    }
}

.videos-1__video-container--unum,
